body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-base: 16px;
  --font-size-13xl: 32px;
  --font-size-2xs-2: 10.2px;
  --font-size-smi: 13px;
  --font-size-xl: 20px;
  --font-size-mini: 15px;
  --font-size-lgi: 19px;
  --font-size-5xl: 24px;
  --font-size-smi-6: 12.6px;
  --font-size-sm-6: 13.6px;
  --font-size-29xl: 48px;

  /* Colors */
  --color-white: #fff;
  --color-steelblue: #4d5a7e;
  --color-gray-100: #222;
  --color-gray-200: rgba(255, 255, 255, 0);
  --color-gray-300: rgba(34, 34, 34, 0.4);
  --color-salmon: #ff6565;
  --color-black: #000;
  --color-darkgray: #9c9c9c;
  --color-red-100: #ff0000;
  --color-red-200: rgba(255, 0, 0, 0.57);

  /* Gaps */
  --gap-lgi: 19px;
  --gap-98xl: 117px;
  --gap-5xs: 8px;
  --gap-9xs: 4px;
  --gap-base: 16px;

  /* Paddings */
  --padding-0: 0px;
  --padding-69xl: 88px;
  --padding-lgi: 19px;
  --padding-12xs-4: 0.4px;
  --padding-81xl-3: 100.3px;
  --padding-xs: 12px;
  --padding-5xl: 24px;
  --padding-5xl-7: 24.7px;
  --padding-smi-6: 12.6px;
  --padding-2xs: 11px;
  --padding-11xs: 2px;
  --padding-4xl: 23px;
  --padding-7xs: 6px;
  --padding-12xs-8: 0.8px;

  /* border radiuses */
  --br-base: 16px;
  --br-11xl: 30px;
  --br-xl: 20px;
  --br-3xs: 10px;
  --br-21xl: 40px;
  --br-31xl: 50px;
}
